import "./Home.css";
import gameImage from "../../assets/gameImage.png";
import Header from "../Header/Header";

function Home() {
  return (
    <div className="home-container">
      <Header />
      <main className="container">
        <div class="content">
          <p>
            FolkTale  um emocionante jogo de aventura em que os jogadores
            exploram um mundo mgico cheio de criaturas fantsticas, desafios
            intrigantes e histrias envolventes. O jogo combina elementos de RPG
            de combate em turnos e explorao para proporcionar uma experincia
            nica e cativante para os jogadores.
          </p>
          <img src={gameImage} alt="Imagem do Jogo" />
        </div>
      </main>
    </div>
  );
}

export default Home;
